.panel-top {
    border-bottom: 1px solid $color-gray2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 25px 0;
    margin: 0 0 40px;
    position: relative;

    &__user {
        margin: 0 0 0 auto;
    }
    &__message {
        margin: 0 0 0 20px;
    }
}

.back-link {
    font-weight: 700;
    font-size: 12px;
    text-transform: uppercase;
    color: $color-black;
    text-decoration: none;
    gap: 10px;
    display: flex;
    align-items: center;

    &__icon {
        box-shadow: 0 0 20px $block-shadow;
        background-color: $color-white;
        fill: $color-black;
        width: 33px;
        height: 33px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: .5s;
    }
    &:hover &__icon {
        fill: $color-main;
        box-shadow: 0 0 5px $block-shadow;
    }
    &__str {
        width: 7px;
        height: 11px;
    }
}

.panel-user {
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: 600;
    color: $color-black;
    text-decoration: none;

    &__icon {
        height: 30px;
        width: 30px;
        border-radius: 50%;
        background-color: $color-main;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 12px;
        color: $color-white;
    }
}

.panel-user-block {
    box-shadow: 0 0 40px $block-shadow;
    background-color: $color-white;
    padding: 0 0 6px;
    border-radius: 0 0 22px 22px;
    overflow: hidden;
    position: absolute;
    right: 0;
    bottom: -1px;
    translate: 0 100%;
    display: none;
    z-index: 15;

    &_show {
        display: block;
    }
    &__link {
        font-weight: 600;
        font-size: 14px;
        color: $color-black;
        display: flex;
        align-items: center;
        gap: 12px;
        height: 50px;
        width: 240px;
        padding: 0 25px;
        text-decoration: none;
        border-bottom: 1px solid #f5f5f5;

        &:last-child {
            border: none;
        }
        &_stroke {
            stroke: $color-main;
            stroke-width: 2;
            fill: none;
        }
        &_fill {
            fill: $color-main;
        }
    }
    &__icon {
        height: 16px;
        width: 16px;
    }
}

.message-icon {
    fill: $color-black;
    text-decoration: none;
    position: relative;

    &:hover {
        fill: $color-main;
    }
    &__icon {
        width: 22px;
        height: 23px;
    }
    &__count {
        background-color: $color-main;
        height: 14px;
        width: 14px;
        border-radius: 50%;
        font-weight: 700;
        font-size: 10px;
        color: $color-white;
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        top: -3px;
        right: 0px;
    }
}